.medicos-container {
  padding: var(--spacing-md);
  max-width: var(--container-max-width);
  margin: 0 auto;
}

.medicos-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: var(--spacing-md);
}

.medicos-header h1 {
  font-size: var(--font-size-xl);
  color: var(--text-color);
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  margin: 0;
  font-weight: var(--font-weight-semibold);
}

.btn-novo-medico {
  display: flex;
  align-items: center;
  gap: var(--spacing-xs);
  background-color: var(--primary-color);
  color: var(--white);
  border: none;
  padding: var(--spacing-xs) var(--spacing-sm);
  border-radius: var(--border-radius-sm);
  font-weight: var(--font-weight-medium);
  text-decoration: none;
  transition: var(--transition-normal);
}

.btn-novo-medico:hover {
  background-color: var(--primary-dark);
}

.medicos-actions {
  display: flex;
  justify-content: space-between;
  margin-bottom: var(--spacing-md);
}

.search-form {
  flex: 1;
  margin-right: var(--spacing-sm);
}

.search-input-container {
  display: flex;
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow: hidden;
}

.search-input-container input {
  flex: 1;
  padding: 10px;
  border: none;
  outline: none;
  font-size: 14px;
}

.btn-search {
  background-color: #f5f5f5;
  border: none;
  padding: 0 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-search:hover {
  background-color: #e0e0e0;
}

.btn-filter {
  display: flex;
  align-items: center;
  gap: 5px;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  padding: 0 15px;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-filter:hover {
  background-color: #e0e0e0;
}

.filter-panel {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 20px;
}

.filter-row {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 15px;
  margin-bottom: 15px;
}

.filter-group {
  display: flex;
  flex-direction: column;
}

.filter-group label {
  margin-bottom: 5px;
  font-weight: 500;
  color: #555;
}

.filter-group select {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: white;
}

.filter-group select:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.filter-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.btn-limpar,
.btn-aplicar {
  padding: 8px 15px;
  border-radius: 4px;
  font-weight: 500;
  cursor: pointer;
  border: none;
  transition: background-color 0.3s;
}

.btn-limpar {
  background-color: #f5f5f5;
  color: #333;
}

.btn-aplicar {
  background-color: #2196f3;
  color: white;
}

.btn-limpar:hover {
  background-color: #e0e0e0;
}

.btn-aplicar:hover {
  background-color: #0d8aee;
}

.medicos-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.medico-card {
  display: flex;
  justify-content: space-between;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  transition: box-shadow 0.3s;
}

.medico-card:hover {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.medico-info {
  flex: 1;
}

.medico-info h3 {
  margin-top: 0;
  margin-bottom: 10px;
  color: #333;
}

.medico-details {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.medico-details p {
  margin: 0;
  font-size: 14px;
  color: #555;
}

.medico-actions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 10px;
  margin-left: 15px;
}

.btn-ver {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  background-color: #2196f3;
  color: white;
  border: none;
  padding: 8px 12px;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 500;
  transition: background-color 0.3s;
}

.btn-ver:hover {
  background-color: #0d8aee;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.btn-dropdown {
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
  width: 100%;
  text-align: center;
}

.dropdown-content {
  display: none;
  position: absolute;
  right: 0;
  background-color: white;
  min-width: 120px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border-radius: 4px;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-content a,
.dropdown-content button {
  display: block;
  padding: 10px;
  text-decoration: none;
  color: #333;
  text-align: left;
  background: none;
  border: none;
  width: 100%;
  cursor: pointer;
  font-size: 14px;
}

.dropdown-content a:hover,
.dropdown-content button:hover {
  background-color: #f5f5f5;
}

.dropdown-content button:hover {
  color: #f44336;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  gap: 15px;
}

.pagination button {
  padding: 8px 15px;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pagination button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination button:not(:disabled):hover {
  background-color: #e0e0e0;
}

.pagination span {
  color: #555;
}

.loading-container,
.error-container,
.no-data-message {
  text-align: center;
  padding: 40px 0;
  color: #555;
}

.error-container button {
  margin-top: 10px;
  padding: 8px 15px;
  background-color: #2196f3;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.no-data-message {
  font-style: italic;
  color: #777;
}

/* Responsividade */
@media (max-width: 768px) {
  .medicos-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 15px;
  }
  
  .btn-novo-medico {
    width: 100%;
    justify-content: center;
  }
  
  .medicos-actions {
    flex-direction: column;
    gap: 10px;
  }
  
  .search-form {
    margin-right: 0;
    margin-bottom: 10px;
  }
  
  .btn-filter {
    width: 100%;
    justify-content: center;
  }
  
  .filter-row {
    grid-template-columns: 1fr;
  }
  
  .medico-card {
    flex-direction: column;
  }
  
  .medico-details {
    grid-template-columns: 1fr;
  }
  
  .medico-actions {
    flex-direction: row;
    margin-left: 0;
    margin-top: 15px;
  }
  
  .btn-ver,
  .dropdown {
    flex: 1;
  }
}