.dashboard-container {
  padding: var(--spacing-md);
}

.page-title {
  margin-bottom: var(--spacing-lg);
  font-size: var(--font-size-xl);
  font-weight: var(--font-weight-medium);
  color: var(--text-color);
}

.stats-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.stat-card {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-md);
  padding: var(--spacing-lg);
  display: flex;
  align-items: center;
  position: relative;
}

.stat-icon {
  width: 50px;
  height: 50px;
  border-radius: var(--border-radius-circle);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-lg);
  margin-right: var(--spacing-md);
}

.stat-icon.opportunity {
  background-color: var(--primary-light);
  color: var(--primary-color);
}

.stat-icon.doctor {
  background-color: var(--info-light);
  color: var(--info-color);
}

.stat-icon.client {
  background-color: var(--warning-light);
  color: var(--warning-color);
}

.stat-info h3 {
  font-size: var(--font-size-xl);
  margin: 0;
  font-weight: var(--font-weight-semibold);
}

.stat-info p {
  margin: var(--spacing-xs) 0 0;
  color: var(--text-secondary);
  font-size: var(--font-size-sm);
}

.stat-link {
  position: absolute;
  bottom: var(--spacing-sm);
  right: var(--spacing-md);
  font-size: var(--font-size-xs);
  color: var(--primary-color);
  text-decoration: none;
}

.stat-link:hover {
  text-decoration: underline;
}

.dashboard-charts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--spacing-lg);
  margin-bottom: var(--spacing-xl);
}

.chart-card {
  background-color: var(--white);
  border-radius: var(--border-radius-md);
  box-shadow: var(--shadow-md);
  padding: var(--spacing-lg);
}

.status-chart {
  margin-top: var(--spacing-md);
}

.status-bar {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
}

.status-label {
  width: 100px;
  font-size: 14px;
  text-transform: capitalize;
}

.status-value {
  height: 24px;
  background-color: var(--primary-color);
  color: white;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  font-size: 12px;
  font-weight: 500;
  min-width: 30px;
  transition: width 0.5s ease;
}

.specialty-chart {
  margin-top: 15px;
}

.specialty-item {
  display: flex;
  justify-content: space-between;
  padding: 8px 0;
  border-bottom: 1px solid var(--medium-gray);
}

.specialty-item:last-child {
  border-bottom: none;
}

.specialty-name {
  font-size: 14px;
}

.specialty-count {
  font-weight: 500;
  color: var(--primary-color);
}

.dashboard-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
}

.dashboard-column {
  display: flex;
  flex-direction: column;
}

.card-title {
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: 500;
  color: var(--text-color);
  border-bottom: 1px solid var(--medium-gray);
  padding-bottom: 10px;
}

.activities-list, .tasks-list {
  display: flex;
  flex-direction: column;
}

.activity-item {
  display: flex;
  align-items: center;
  padding: 12px 0;
  border-bottom: 1px solid var(--medium-gray);
}

.activity-item:last-child {
  border-bottom: none;
}

.activity-icon {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  margin-right: 10px;
  background-color: #f5f5f5;
  color: var(--dark-gray);
}

.activity-icon.opportunity {
  background-color: #e6f7f0;
  color: var(--primary-color);
}

.activity-icon.doctor {
  background-color: #e3f2fd;
  color: #1976d2;
}

.activity-icon.client {
  background-color: #fff8e1;
  color: #ffa000;
}

.activity-details {
  flex: 1;
}

.activity-title {
  margin: 0;
  font-size: 14px;
  font-weight: 500;
}

.activity-subtitle {
  margin: 2px 0 0;
  font-size: 13px;
}

.activity-time {
  font-size: 12px;
  color: var(--dark-gray);
  display: block;
  margin-top: 3px;
}

.no-data-message {
  color: var(--dark-gray);
  font-style: italic;
  text-align: center;
  padding: 20px 0;
}

.task-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 0;
  border-bottom: 1px solid var(--medium-gray);
}

.task-item:last-child {
  border-bottom: none;
}

.task-details {
  flex: 1;
}

.task-title {
  margin: 0 0 5px;
  font-size: 14px;
}

.task-meta {
  display: flex;
  align-items: center;
  font-size: 12px;
}

.task-priority {
  padding: 2px 8px;
  border-radius: 10px;
  margin-right: 10px;
}

.priority-high {
  background-color: #ffebee;
  color: #f44336;
}

.priority-medium {
  background-color: #fff8e1;
  color: #ffa000;
}

.priority-low {
  background-color: #e6f7f0;
  color: var(--primary-color);
}

.task-due-date {
  color: var(--dark-gray);
}

.btn-sm {
  padding: 5px 10px;
  font-size: 12px;
}

.loading-container,
.error-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  font-size: 18px;
  color: var(--dark-gray);
}

.error-container {
  color: #f44336;
}

.card-title svg {
  margin-right: 8px;
  vertical-align: middle;
}

/* Responsividade */
@media (max-width: 768px) {
  .stats-container,
  .dashboard-charts,
  .dashboard-content {
    grid-template-columns: 1fr;
  }
  
  .status-label {
    width: 80px;
    font-size: 12px;
  }
}