.mensagens-container {
  padding: 20px;
  height: calc(100vh - 80px);
  display: flex;
  flex-direction: column;
}

.mensagens-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.mensagens-header h1 {
  margin: 0;
  color: #333;
}

.btn-nova-mensagem {
  background-color: #4a90e2;
  color: white;
  padding: 10px 20px;
  border-radius: 4px;
  text-decoration: none;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 8px;
  transition: background-color 0.3s;
}

.btn-nova-mensagem:hover {
  background-color: #357abd;
}

.mensagens-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.tipo-visualizacao {
  display: flex;
  gap: 10px;
}

.tipo-visualizacao button {
  background: none;
  border: none;
  padding: 8px 15px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 8px;
  border-radius: 4px;
  color: #555;
  font-weight: 500;
  transition: all 0.2s;
}

.tipo-visualizacao button:hover {
  background-color: #f0f0f0;
}

.tipo-visualizacao button.active {
  background-color: #e6f0ff;
  color: #4a90e2;
  border-bottom: 2px solid #4a90e2;
}

.search-filter {
  display: flex;
  align-items: center;
  gap: 10px;
}

.search-box {
  position: relative;
  display: flex;
  align-items: center;
}

.search-box input {
  padding: 8px 15px;
  padding-right: 40px;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 250px;
  font-size: 14px;
}

.search-box button {
  position: absolute;
  right: 5px;
  background: none;
  border: none;
  cursor: pointer;
  color: #777;
}

.filter-button {
  background: none;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 8px;
  cursor: pointer;
  color: #555;
  display: flex;
  align-items: center;
  justify-content: center;
}

.filter-button:hover {
  background-color: #f0f0f0;
}

.filter-panel {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 15px;
  margin-bottom: 15px;
}

.filter-options {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.filter-group label {
  font-size: 14px;
  color: #555;
}

.filter-group select {
  padding: 8px;
  border: 1px solid #ddd;
  border-radius: 4px;
  min-width: 150px;
}

.mensagens-content {
  display: flex;
  flex: 1;
  gap: 20px;
  overflow: hidden;
}

.mensagens-lista {
  flex: 0 0 40%;
  border: 1px solid #ddd;
  border-radius: 4px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
}

.mensagens-lista ul {
  list-style: none;
  padding: 0;
  margin: 0;
  flex: 1;
}

.mensagem-item {
  display: flex;
  padding: 15px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  transition: background-color 0.2s;
}

.mensagem-item:hover {
  background-color: #f5f5f5;
}

.mensagem-item.selecionada {
  background-color: #e6f0ff;
}

.mensagem-item.nao-lida {
  background-color: #f0f7ff;
  font-weight: 500;
}

.mensagem-icone {
  margin-right: 15px;
  color: #4a90e2;
  display: flex;
  align-items: flex-start;
  padding-top: 3px;
}

.mensagem-info {
  flex: 1;
}

.mensagem-cabecalho {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}

.mensagem-remetente {
  font-weight: 500;
  color: #333;
}

.mensagem-data {
  font-size: 12px;
  color: #777;
}

.mensagem-assunto {
  font-weight: 500;
  margin-bottom: 5px;
  color: #333;
}

.mensagem-preview {
  font-size: 14px;
  color: #666;
  margin-bottom: 8px;
  line-height: 1.4;
}

.mensagem-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
}

.prioridade {
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 11px;
  font-weight: 500;
}

.prioridade.alta {
  background-color: #ffebee;
  color: #d32f2f;
}

.prioridade.normal {
  background-color: #e8f5e9;
  color: #388e3c;
}

.prioridade.baixa {
  background-color: #e3f2fd;
  color: #1976d2;
}

.anexos-badge {
  background-color: #f0f0f0;
  padding: 2px 8px;
  border-radius: 10px;
  font-size: 11px;
}

.paginacao {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  border-top: 1px solid #eee;
  background-color: #f9f9f9;
}

.paginacao button {
  background-color: #fff;
  border: 1px solid #ddd;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
}

.paginacao button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.paginacao span {
  font-size: 14px;
  color: #555;
}

.mensagem-detalhes {
  flex: 1;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 20px;
  overflow-y: auto;
  background-color: #fff;
}

.selecione-mensagem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #999;
}

.icone-grande {
  font-size: 48px;
  margin-bottom: 15px;
  opacity: 0.5;
}

.mensagem-completa {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.mensagem-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #eee;
}

.mensagem-header h2 {
  margin: 0;
  color: #333;
}

.mensagem-acoes {
  display: flex;
  gap: 10px;
}

.btn-responder, .btn-excluir {
  display: flex;
  align-items: center;
  gap: 5px;
  padding: 8px 12px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s;
}

.btn-responder {
  background-color: #4a90e2;
  color: white;
  text-decoration: none;
}

.btn-responder:hover {
  background-color: #357abd;
}

.btn-excluir {
  background-color: #f44336;
  color: white;
  border: none;
}

.btn-excluir:hover {
  background-color: #d32f2f;
}

.mensagem-meta {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 4px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.mensagem-meta div {
  display: flex;
  gap: 5px;
  font-size: 14px;
  color: #555;
}

.mensagem-meta strong {
  min-width: 80px;
  color: #333;
}

.mensagem-corpo {
  flex: 1;
  line-height: 1.6;
  color: #333;
  margin-bottom: 20px;
  white-space: pre-wrap;
}

.mensagem-anexos {
  border-top: 1px solid #eee;
  padding-top: 15px;
}

.mensagem-anexos h3 {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 16px;
  color: #333;
}

.mensagem-anexos ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.mensagem-anexos li {
  margin-bottom: 8px;
}

.mensagem-anexos a {
  color: #4a90e2;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 5px;
}

.mensagem-anexos a:hover {
  text-decoration: underline;
}

.loading, .no-messages, .error-message {
  padding: 20px;
  text-align: center;
  color: #666;
}

.error-message {
  color: #d32f2f;
  background-color: #ffebee;
  border-radius: 4px;
  margin-bottom: 15px;
}

/* Responsividade */
@media (max-width: 768px) {
  .mensagens-content {
    flex-direction: column;
  }
  
  .mensagens-lista, .mensagem-detalhes {
    flex: none;
    width: 100%;
    max-height: 50vh;
  }
  
  .mensagens-actions {
    flex-direction: column;
    gap: 10px;
    align-items: stretch;
  }
  
  .search-box input {
    width: 100%;
  }
  
  .search-filter {
    width: 100%;
  }
  
  .search-box {
    flex: 1;
  }
}